@mixin transition-img {
    transition: transform 8s cubic-bezier(0.25, 0.46, 0.45, 0.94), -webkit-transform 8s cubic-bezier(0.25, 0.46, 0.45, 0.94);       
}
.article-card {
    height: 100%;
    display: flex;
    flex-direction: column;
    min-width: 0;
    .article-card_header {
        position: relative;
        margin-bottom: 17px;
        overflow: hidden;
        img {
            object-fit: cover;
            @include transition-img;
        }
        &:hover {
            @media (hover: hover) {
                img {
                    transform: scale(1.2);    
                } 
            }
        }
    }
    .article-card_published-at {
        font-size: var(--body-font-size);
        color: var(--text-miscellaneous);
        margin-bottom: 2px;
    }
    .article-card_title { 
        margin-bottom: 7px; 
        font-weight: var(--font-body-weight);
        overflow: hidden;
	    white-space: initial;
	    display: -webkit-box;
	    -webkit-line-clamp: 1; 
	    -webkit-box-orient: vertical;
	    word-wrap: break-word;
        text-overflow: -o-ellipsis-lastline; 
    }
    .article-card_excerpt {  
        overflow: hidden;
        margin-bottom: 39px;
	    white-space: initial;
	    display: -webkit-box;
	    -webkit-line-clamp: 2;
	    -webkit-box-orient: vertical; 
	    word-wrap: break-word;
        text-overflow: -o-ellipsis-lastline;
        color: var(--body-color-2); 
        img {
            display: none;
        }
        p:last-child {
            margin: 0;
        }
    }
    .article-card_footer {
        margin-top: auto;  
    }
    .btn {
        display:inline-block;
        padding: 9px 17px;  
        min-width: 114px;
        font-size: calc(var(--btn2-fs) - 2px);
        @media (min-width: 750px) {
            padding-left: 40px;
            padding-right: 40px;
        }
    } 
}